body {
  background-color: #282c34;
}

.response-container-container {
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  width: 80vw;
  background-color: #22262e;
  margin-top: 10vh;
  z-index: 5;
}

.response-container {
  margin: 8px;
  width: 95%;
  height: 50%;
}

.top-bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: 10vh;
  background-color: #333c52;
  z-index: 10;
}

.top-bar > div {
  margin: 5%;
}

.sticky {
  position: fixed;
  top: 0;
}

.file-manager {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.file-manager > * {
  margin: 10px;
}

.debug-paper-container {
  display: flex;
  width: 50vw;
  height: 50vh;
  background-color: yellow;
}

.debug-paper-container > * {
  width: 25vw;
  height: 25vh;
}

.download-interface {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.download-interface > * {
  margin: 10px
}