.response-box {
    display: flex;
    flex-direction: column;
    align-content: flex-start;
    width: 100%;
    height: 100%;
    /* background-color: #282c34;
    border: 2px solid gray; */
}

.id-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 5px;
    width: 7.5%;
    height: 15%;
    background-color: darkgray;
    border: 2px solid gray;
}

.explanation-container {
    width: 90%;
    height: 35%;
    overflow: auto;
    margin: 5px;
    padding: 5px;
    text-align: left;
    background-color: darkgray;
    border: 2px solid gray;
}

.coding-container {
    display: flex;
    align-content: flex-start;
    align-items: center;
    padding: 5px;
    margin: 5px;
    width: 90%;
    height: 30%;
    border: 2px solid gray;
}

.has-content-container {
    margin: 5px;
    width: 15%;
    height: 100%;
    background-color: lightgray;
    border: 2px solid gray;
}

.content-codes-container {
    flex: 1;
    display: flex;
    flex-wrap: wrap;
    overflow-y: auto;
    margin: 5px;
    height: 100%;
    background-color: lightgray;
    border: 2px solid gray;
}

.tag-container {
    margin: 5px;
    max-width: 30%;
}